@mixin retina {
  @media (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1200 {
  @media (min-width: $vp-1200) {
    @content;
  }
}

@mixin vp-1199 {
  @media (max-width: $vp-1200 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

